* {
  font-family: Verdana, Geneva, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f2f2f2;
}

li {
  list-style-type: none;
}

.container {
  width: 90%;
  margin: 0 auto;
}

.loader {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.movies {
  padding: 5rem 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5em;
}

.movie {
  padding: 1rem;
  display: flex;
  background-color: #fff;
  -webkit-box-shadow: 0px 19px 18px 3px rgba(199, 199, 199, 1);
  -moz-box-shadow: 0px 19px 18px 3px rgba(199, 199, 199, 1);
  box-shadow: 0px 19px 18px 3px rgba(199, 199, 199, 1);
}

.movie li {
  display: inline;
  margin-right: 0.25rem;
}

.movie img {
  width: 100%;
  max-width: 180px;
  position: relative;
  top: -2rem;
  -webkit-box-shadow: 0px 19px 18px 3px rgba(199, 199, 199, 1);
  -moz-box-shadow: 0px 19px 18px 3px rgba(199, 199, 199, 1);
  box-shadow: 0px 19px 18px 3px rgba(199, 199, 199, 1);
}

.movie__data {
  padding-left: 2rem;
}

.movie h3 {
  padding: 1rem 0;
}

.movie li,
.movie p,
.movie h5 {
  color: #999;
  padding: 0.5rem 0;
}

@media (max-width: 1300px) {
  .movies {
    grid-template-columns: repeat(1, 1fr);
  }
}
